'use client'

import { useState, useEffect } from 'react'
import { Formik, Form, Field, ErrorMessage } from 'formik'
import { useMutation } from '@tanstack/react-query'
import toast from 'react-hot-toast'
import { useRouter } from 'next/navigation'
import styles from './OnboardingForm.module.css'

import { onboardingSchema } from '@/lib/validation/onboarding.schema'
import { submitOnboarding } from '@/lib/api/onboarding'

interface OnboardingFormValues {
  name: string
  birthDate: string
  gender: string
  avatar: File | null
}

export default function OnboardingForm() {
  const router = useRouter()
  const defaultAvatar = '/images/avatar-image.svg'
  const [preview, setPreview] = useState<string>(defaultAvatar)

  const mutation = useMutation({
    mutationFn: (formData: FormData) => submitOnboarding(formData),
    onSuccess: () => {
      toast.success('Форма успішно надіслана!')
      router.push('/')
    },
    onError: (err: Error) => toast.error(err.message || 'Помилка при відправці'),
  })

  return (
    <Formik<OnboardingFormValues>
      initialValues={{ name: '', birthDate: '', gender: '', avatar: null }}
      validationSchema={onboardingSchema}
      onSubmit={(values) => {
        const formData = new FormData()
        formData.append('name', values.name)
        formData.append('birthDate', values.birthDate)
        formData.append('gender', values.gender)
        if (values.avatar) formData.append('avatar', values.avatar)
        mutation.mutate(formData)
      }}
    >
      {({ setFieldValue, values }) => {
        useEffect(() => {
          if (values.avatar) {
            const objectUrl = URL.createObjectURL(values.avatar)
            setPreview(objectUrl)
            return () => URL.revokeObjectURL(objectUrl)
          } else {
            setPreview(defaultAvatar)
          }
        }, [values.avatar])

        return (
          <Form className={styles.form}>
            <div className={styles.page}>
      <main className={styles.formSide}>
        <h1 className={styles.title}>Давайте познайомимось ближче</h1>
        <OnboardingForm />
      </main>

      <aside className={styles.imageSide}>
        <div className={styles.imageWrapper}>
          <img
            src="/images/onboarding-image.jpg"
            alt="Onboarding"
            className={styles.image}
          />
        </div>
      </aside>
    </div>
            <div className={styles.avatar}>
              <img
                src={preview}
                alt="Avatar preview"
                width={164}
                height={164}
                style={{ borderRadius: '50%', objectFit: 'cover', marginBottom: 16 }}
              />

              <label htmlFor="avatar-upload">
                <button type="button" className={styles.uploadButton}>
                  Завантажити фото
                </button>
              </label>
              <input
                id="avatar-upload"
                type="file"
                accept="image/*"
                style={{ display: 'none' }}
                onChange={(e) =>
                  setFieldValue('avatar', e.currentTarget.files?.[0] || null)
                }
              />
              <ErrorMessage name="avatar" component="div" className={styles.error} />
            </div>

            <div className={styles.formUser}>
              <label>Імʼя</label>
              <Field name="name" placeholder="Імʼя" />
              <ErrorMessage name="name" component="div" className={styles.error} />

              <label>Дата народження</label>
              <Field name="birthDate" type="date" />
              <ErrorMessage name="birthDate" component="div" className={styles.error} />

              <label>Стать</label>
              <Field name="gender" as="select">
                <option value="">Оберіть стать</option>
                <option value="male">Хлопчик</option>
                <option value="female">Дівчинка</option>
                <option value="unknown">Ще не знаю</option>
              </Field>
              <ErrorMessage name="gender" component="div" className={styles.error} />

              <button
                type="submit"
                disabled={mutation.isPending}
                className={styles.submitButton}
              >
                {mutation.isPending ? 'Відправка...' : 'Зберегти'}
              </button>
            </div>
          </Form>
        )
      }}
    </Formik>
  )
}